*{
    margin: 0;
    padding: 0;
}
.about-us{
    height: 100vh;
    width: 100%;
    display: flex;        
 }
.about-us-text{
    margin-top: 10%;
    max-width: 700px;
    text-align: center;
    float: left;
}

.about-us-img{
    max-width: 50%;;
    height: 100%;
    overflow: hidden;


}
.about-us-img img{
    max-width: 100%; 
    display:block; 
    margin-top: 10%;
    height: auto;
}


.about-us-text p{
    font-size: 1.8rem;
    line-height: 1.5;
    text-align: center;
}
@media (max-width:900px){
    
.about-us-text p{
    font-size: 1rem;
  
    text-align: center;
}


}