*{
padding: 0;
margin: 0;
font-size: 2rem;
box-sizing: border-box;


}
header{
    min-height: 60px;
    background:#2FAD8A ;
    color: white;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    flex-wrap: wrap;
    padding: 10px 10px;
   


}
.fa-graduation-cap{
    margin-left: 0.5rem;
    font-size: 1.6rem;

}

header .logo{
    padding-left: 10px;
    height: 60px;
    display: flex;
    color: white;
    justify-content: center;
    align-items: center;
    font-family: 'Courier Prime';

    
    font-size: 1.4rem;
}
header ul{
    list-style: none;
    margin-right: 50px;
    font-size: 1.2rem;
    
  
}
header h1{
    color: white;
   margin-top: 60px ;
    margin-left: 0.8rem;
    
}
header ul li{
    display: inline-block;
    padding: 10px;
    
}
header ul li a{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    
    
    padding:0 15px;

}
header ul li a:hover{
    color:#22916f;
    background-color:rgba(255, 255, 255, 0.658);
    text-decoration:none;
    cursor: pointer;
   border-radius: 8px;
   border: white;
  
   width: 100px;
    transition: all 0.2s ease-out;
    


}

header .menu,
header .close{
    cursor: pointer;
    text-transform: uppercase;
    border:2px solid white;
    padding: 10px 2px;
    margin-right: 20px;
    display:none;
    
    
}
header .close{
    position: absolute;
    top:10px;
    right:0px ;
    border-radius: 1px;
    padding: 2px;


}
.stiky{
    position: -webkit-sticky; /* Safari */
    position:sticky ;
    top:0;
   
    
    z-index: 90;
}
@media (max-width:960px){
    header .logo,
    header ul{
        width: 100%;
        margin: 0;
        text-align: center;
        line-height: 50px;
        
    }
    
}
@media (max-width:470px){
    header ul{
        position: fixed;
        top: 0;
        left: -100%;
        background:#2FAD8A;
        width: 100%;
        height: 100vh;
        display:flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: column;
        z-index: 99;
        transition: 0.5s linear;
    }
    header ul li a:hover{
        color:#22916f;
        background-color:rgba(255, 255, 255, 0.658);
        text-decoration:none;
        cursor: pointer;
       border-radius: 8px;
       border: white;
      
       width: 100px;
        transition: all 0.2s ease-out;
        
    
    }
    
    
    header ul.toggle{
        left: 0;
    }
    header ul li{
        line-height: normal;

    }
    header .menu,
    header .close{
        display: block;
    }
    header .logo{
        width: initial;
        margin-left: 20px;
        line-height: 70px;
    }

}